import {BrowserModule} from '@angular/platform-browser';
import {ErrorHandler, NgModule} from '@angular/core';
import {IonicApp, IonicErrorHandler, IonicModule} from 'ionic-angular';
import { HttpClientModule,HttpClient } from '@angular/common/http';

import {MyApp} from './app';
import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import { RootViewCoverService } from '../pages/discoverMusic/provider/eventEmitService';
import { SharedModule } from '../sharedModule/sharedModule'

@NgModule({
    declarations: [
        MyApp
    ],
    imports: [
        SharedModule,
        HttpClientModule,
        BrowserModule,
        IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../components/playMusic/playMusic.module.ngfactory#PlayMusicModuleNgFactory', name: 'play-music-page', segment: 'playMusic', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/discoverMusic/children/music/allMusicType/allMusicType.module.ngfactory#AllMusicTypeModuleNgFactory', name: 'all-music-type-page', segment: 'allMusicType', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/discoverMusic/children/music/everyDayRecommendMusic/everyDayRecommendMusic.module.ngfactory#EveryDayRecommendMusicModuleNgFactory', name: 'day-rcmd-music-page', segment: 'everyDayRecommendMusic', priority: 'low', defaultHistory: ['discover-music-page'] },
    { loadChildren: '../pages/discoverMusic/children/music/highqualityMusicList/highqualityMusic.module.ngfactory#HighqualityMusicModuleNgFactory', name: 'highquality-music-page', segment: 'highqualityMusic', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/discoverMusic/children/music/moreMusicList/moreMusicList.module.ngfactory#MoreMusicListPageModuleNgFactory', name: 'more-music-list', segment: 'more-music-list', priority: 'low', defaultHistory: ['discover-music-page', 'd-music-page'] },
    { loadChildren: '../pages/discoverMusic/children/music/moreSinglePlay/moreSinglePlay.module.ngfactory#MoreSinglePlayModuleNgFactory', name: 'more-single-play-page', segment: 'moreSinglePlay', priority: 'low', defaultHistory: ['discover-music-page'] },
    { loadChildren: '../pages/discoverMusic/children/music/music.module.ngfactory#MusicModuleNgFactory', name: 'd-music-page', segment: 'd-music-page', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/discoverMusic/children/music/personalFm/personalFm.module.ngfactory#PersonalFmModuleNgFactory', name: 'personal-fm-page', segment: 'personalFm', priority: 'low', defaultHistory: ['discover-music-page'] },
    { loadChildren: '../pages/discoverMusic/children/music/rankingList/rankingList.module.ngfactory#RankingListModuleNgFactory', name: 'ranking-list-page', segment: 'ranking-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/discoverMusic/children/music/rankingListDetail/rankingListDetail.module.ngfactory#RankingListDetailModuleNgFactory', name: 'ranking-list-detail-page', segment: 'ranking-list-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/discoverMusic/children/music/recommendMusicListDetail/recommendMusicListDetail.module.ngfactory#RecommendMusicListDetailModuleNgFactory', name: 'recd-music-list-page', segment: 'recommendMusicListDetail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/discoverMusic/children/videos/videos.module.ngfactory#VideosModuleNgFactory', name: 'videos-page', segment: 'videos-page', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/discoverMusic/discoverMusic.module.ngfactory#DiscoverMusicModuleNgFactory', name: 'discover-music-page', segment: 'discover-music-page', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/discoverMusic/root/root.module.ngfactory#DiscoverRootPageModuleNgFactory', name: 'discover-root-page', segment: 'root', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/discoverMusic/search/search.module.ngfactory#SearchModuleNgFactory', name: 'search-page', segment: 'search', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/myMusic/myMusic.module.ngfactory#MyMusicModuleNgFactory', name: 'my-music', segment: 'myMusic', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module.ngfactory#TabsModuleNgFactory', name: 'tabs-page', segment: 'tab', priority: 'low', defaultHistory: [] }
  ]
})
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp
    ],
    providers: [
        RootViewCoverService,
        HttpClientModule,
        HttpClient,
        StatusBar,
        SplashScreen,
        {provide: ErrorHandler, useClass: IonicErrorHandler}
    ],
    exports:[SharedModule]
})
export class AppModule {
}



